/* seat-availability.css */

@keyframes wipe-in {
  0% {
    clip-path: inset(0 100% 0 0);
  }
  100% {
    clip-path: inset(0 0 0 0);
  }
}

@keyframes wipe-out {
  0% {
    clip-path: inset(0 0 0 0);
  }
  100% {
    clip-path: inset(0 100% 0 0);
  }
}

/* seat-availability.css */

.seat-availability-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin-top: 10px;
  width: 100%; /* Full width */
}

.college-section {
  text-align: center;
  margin-top: 0;
  width: 100%; /* Full width */
  padding: 20px;
  box-sizing: border-box;
}

.college-name {
  margin-top: 0;
  color: red;
  font-size: 2.8em;
  word-wrap: break-word; /* Ensure long names wrap */
  margin-bottom: 0;
  background-color: #fffffd;

}

.branch-section {
  margin-top: 10px;
  width: 100%;
}

.branch-details h3,
.branch-section h3 {
  font-size: 2.6em;
  margin: 10px 0;
  color: #007bff;
  transition: transform 0.5s, opacity 0.5s;
  opacity: 1;
  transform-style: preserve-3d;
}

.category-tables {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 20px;
}

.category-table {
  margin: 10px;
  background-color: #ffffff;
  border-radius: 8px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  padding: 20px;
  min-width: 300px;
  flex: 1;
  transition: transform 0.5s, opacity 0.5s;
  opacity: 1;
  transform-style: preserve-3d;
}

.category-table table {
  width: 100%;
  border-collapse: collapse;
}

.category-table th,
.category-table td {
  border: 1px solid #ccc;
  padding: 10px;
  text-align: center;
  font-size: 1.2em;
}

.category-table th {
  background-color: #007bff;
  color: white;
}

.category-table .category-name {
  font-weight: bold;
}

.category-table .availability {
  font-weight: bold;
  font-size: 2em;
  padding:0;
}

.category-table .red {
  color: rgb(248, 4, 4);
}

.category-table .darkorange {
  color: rgb(44, 107, 243);
}

.category-table .gold {
  color: rgb(25, 43, 212);
}

.category-table .green {
  color: green;
}

.no-seats-available {
  text-align: center;
  margin-top: 20px;
  font-size: 2em;
  color: #777;
}

.disclaimer {
  margin-top: 30px;
  font-size: 1.2em;
  color: #fff;
  text-align: center;
  background-color: rgb(255, 30, 30);
  padding: 10px;
  border-radius: 5px;
  animation: wipe-in 0.5s forwards;
}

.visit-note {
  margin-top: 10px;
  font-size: 1.2em;
  color: #fff;
  text-align: center;
  background-color: #000;
  padding: 10px;
  border-radius: 5px;
  animation: wipe-in 0.5s forwards;
}

.notes {
  margin-top: 20px;
  font-size: 2.2em;
  color: #007bff;
  text-align: center;
  background-color: #f0f0f0;
  padding: 10px;
  border-radius: 5px;
  animation: wipe-in 0.5s forwards;
}


.wipe-out {
  animation: wipe-out 0.5s forwards;
}

.wipe-in {
  animation: wipe-in 0.5s forwards;
}
